import {
	baseUrl
} from './config'
class AJAX {
	constructor() {
		this.baseUrl = baseUrl;
	}
	get(url, params) {
		let obj = Object.assign({}, {
			params
		}, {
			url: url,
			method: 'GET'
		});
		return new Promise((resolve, reject) => {
			this._request(obj, resolve, reject)
		})
	}
	post(url, params) {
		let obj = Object.assign({}, {
			params
		}, {
			url: url,
			method: 'POST'
		});
		return new Promise((resolve, reject) => {
			this._request(obj, resolve, reject)
		})
	}
	_request(obj, resolve, reject) {
		console.info(obj)
		let that = this;
		let token = "";
		uni.getStorage({
			key: 'accessToken',
			success: function(res) {
				token = res.data;
			}
		})
		uni.request({
			url: that.baseUrl + obj.url,
			data: obj.params,
			method: obj.method,
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'accessToken': token
			},
			success: (res) => {
				resolve(res.data)
			},
			fail: (err) => {
				reject(err)
				uni.showToast({
					title: '网络异常,请稍后再试',
					icon: 'none'
				});
			}
		})
	}
}
const ajax = new AJAX()
export default ajax
